// nav icon variables
$height-icon: 24px;
$width-line: 36px;
$height-line: 4px;
$transition-time: 0.4s;
$rotation: 45deg;
$translateY: ($height-icon / 2);
$translateX: 0;

.nav-toggle-container {
  display: block;
  position: fixed;
  left: 0.5em;
  top: 1em;
  margin-bottom: rem(5);
  z-index: 2000;
  padding: rem(7) rem(7) rem(11);
  //    background: $root-background-color;
  //    border-radius: rem(7);
}

.hexagon {
  position: absolute;
  top: -11px;
  right: -3px;
  width: 55px;
  height: 31.75px;
  background-color: white;
  margin: 15.88px 0;
  opacity: 1;
  @include transition(all 0.625s);
  &:hover {
  }
}

.hexagon-rotate {
  @include transform(rotate(180deg));
}

.hexagon.active {
  opacity: 0;
}

.hexagon:before,
.hexagon:after {
  content: "";
  position: absolute;
  width: 0;
  border-left: 27.5px solid transparent;
  border-right: 27.5px solid transparent;
}

.hexagon:before {
  bottom: 100%;
  border-bottom: 15.88px solid white;
}

.hexagon:after {
  top: 100%;
  width: 0;
  border-top: 15.88px solid white;
}

.nav-toggle {
  display: block;
  width: $width-line;
  height: $height-icon;
  position: relative;
  .line {
    display: block;
    width: $width-line;
    height: $height-line;
    position: absolute;
    left: 0;
    background: $accent-color;
    @include transition(all $transition-time);
    &.line-1 {
      top: 0;
    }
    &.line-2 {
      top: 50%;
    }
    &.line-3 {
      top: 100%;
    }
  }
  &:hover,
  &:focus {
    background-color: transparent;
    .line-1 {
      @include transform(translateY($height-line / 2 * -1));
    }
    .line-3 {
      @include transform(translateY($height-line / 2));
    }
  }
  &.active {
    .line {
      background: white;
    }
    .line-1 {
      @include transform(
        translateY($translateY) translateX($translateX) rotate($rotation)
      );
    }
    .line-2 {
      opacity: 0;
    }
    .line-3 {
      @include transform(
        translateY($translateY * -1) translateX($translateX)
          rotate($rotation * -1)
      );
    }
  }
}

/* Overlay style */

.overlay-nav {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  opacity: 0;
  background: $accent-color;
  visibility: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  @include transition(opacity 0.5s, visibility 0s 0.5s);
  &.open {
    opacity: 0.95;
    visibility: visible;
    @include transition(opacity 0.5s);
    nav {
      ul {
        opacity: 1;
        @include transform(rotateX(0deg));
      }
    }
  }
  nav {
    position: relative;
    text-align: center;
    @include perspective(1200px);
  }
  ul {
    margin-left: 0;
    opacity: 0.4;
    margin: 0;
    @include transform(translateY(-25%) rotateX(35deg));
    @include transition(transform 0.5s, opacity 0.5s);
  }
  li {
    display: block;
    a {
      display: inline-block;
      padding: 0.5rem;
      color: white;
      text-transform: uppercase;
      font-size: 3.5rem;
      line-height: 1.1;
      letter-spacing: 0.05em;
      @include transition(color 0.2s);
      &:hover,
      &:focus {
        color: $theme-color;
      }
    }
  }
  &.close {
    nav {
      ul {
        @include transform(translateY(25%) rotateX(-35deg));
      }
    }
  }
}

@media screen and (max-height: 700px) {
  .overlay-nav {
    ul {
      li {
        a {
          font-size: 2.5em;
        }
      }
    }
  }
}
