// Social Media Icons

.icon {
  > svg {
    display: inline-block;
    width: 1rem;
    height: 1rem;
    vertical-align: middle;

    path {
      fill: $grey-color;
    }
  }
}

footer {
  .social-media-list {
    icon {
      margin-top: -5px;
    }
    a {
      &:hover {
        color: #fff;
        text-decoration: none;
        background: #540ead;
      }
    }
  }
}
