// CONTACT FORM - MIGHT NEED TO UPDATE THIS CODE TO MAKE IT LESS GENERAL IN THE FUTURE

$cubic: cubic-bezier(0.64, 0.09, 0.08, 1);

form {
  width: 100%;
  max-width: 460px;
  margin: 100px auto 0;
  padding: 0 0 40px;
  border-radius: 3px;
  color: $root-color;
  @include media($width-500) {
    max-width: 360px;
  }
}

input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  @include transition(all 0.3s);
}

input,
textarea {
  display: block;
  max-width: 400px;
  width: 100%;
  margin: 28px 0 10px;
  padding: 20px 0 10px;
  border: none;
  border-bottom: solid 1px $accent-color;
  background: linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0) 96%,
    $accent-color 4%
  );
  background-position: -400px 0;
  background-size: 400px 100%;
  background-repeat: no-repeat;
  @include transition(background 0.3s $cubic);
  resize: none;
  overflow: hidden;
  @include media($width-500) {
    max-width: 300px;
  }
  &:focus::-webkit-input-placeholder {
    color: $accent-color;
  }
  &:focus,
  &:valid {
    box-shadow: none;
    outline: none;
    background-position: 0 0 !important;
    &::-webkit-input-placeholder {
      font-size: 0.9em;
      transform: translateY(-22px);
      visibility: visible !important;
      opacity: 1;
    }
  }
}

textarea {
  &:focus,
  &:valid {
    overflow: scroll;
    overflow-x: hidden;
  }
}

input[type="email"] {
  background: linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0) 96%,
    $accent-color 4%
  );
  background-position: -400px 0;
  background-repeat: no-repeat;
  transition: border-bottom 0.3s ease, background 0.3s ease;
  @include media($width-500) {
    background-position: -400px 0;
  }
  &:focus {
    background-position: 0 0;
  }
}

// Extend button style to be better sized and positioned for the form
button.btn {
  display: block;
  width: 60%;
  position: relative;
  margin: 2rem auto 0;
  background: transparent;
}
