footer {
    @include row();
    background: $theme-color;
}

.footer-inner {
    @include span-columns(6 of 8);
    @include shift(1 of 8);
    margin-top: 1em;
    text-align: center;
    a {
        padding: 1em;
        &:hover {}
    }
}

.footer-name {
    font-size: 0.8em;
    margin-top: 0.5em;
}