body {
  color: $root-color;
  background-color: $background-color;
  -webkit-text-size-adjust: 100%;
  -webkit-font-feature-settings: "kern" 1;
  -moz-font-feature-settings: "kern" 1;
  -o-font-feature-settings: "kern" 1;
  font-feature-settings: "kern" 1;
  font-kerning: normal;
  font-weight: 400;
  font-style: normal;
  font-size: 1.3125rem;
  line-height: 1.875rem;
  @include media($mobile-large) {
    font-size: 1.1125rem;
    line-height: 1.575rem;
  }
}

h1 {
  display: inline-block;
  position: relative;
  width: 100%;
  margin: 0 auto;
  font-family: inherit;
  font-weight: 400;
  font-style: inherit;
  font-size: 5vw;
  line-height: 5vw;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 1.2vw;
  @include media($full-width) {
    font-size: 10vw;
    line-height: 11vw;
    letter-spacing: 2vw;
  }
  // Title offset highlight effect
  span {
    position: relative;
    z-index: 20;
    &::before {
      content: "";
      display: inline-block;
      position: absolute;
      width: 97%;
      height: 64%;
      top: 36%;
      left: 5%;
      z-index: -1;
      background-color: $theme-color; // Default color for highlight
      @include media($full-width) {
        top: 39%;
        left: 8%;
      }
    }
  }
}

section:nth-of-type(even),
#projects {
  h1 span::before {
    background-color: white;
  }
  .section-title {
    background-color: $theme-color;
  }
  .section-body {
    background-color: white;
  }
}

h2 {
  margin-top: 1.875rem;
  margin-bottom: 1.875rem;
  font-family: inherit;
  font-weight: 400;
  font-style: inherit;
  font-size: 3.4375rem;
  line-height: 3.75rem;
  @include media($mobile-large) {
    font-size: 2.9rem;
    line-height: 3.1rem;
  }
}

h3 {
  font-family: inherit;
  font-weight: 700;
  font-style: inherit;
  font-size: 2.125rem;
  line-height: 1.4;
  margin-top: 0rem;
  margin-bottom: 1.875rem;

  @include media($mobile-large) {
    font-size: 1.7rem;
    line-height: 1.9rem;
  }
}

h4 {
  font-family: inherit;
  font-weight: 600;
  font-style: inherit;
  font-size: 1.3125rem;
  line-height: 1.875rem;
  margin-top: 1.875rem;
  margin-bottom: 1.625em;
  @include media($mobile-large) {
    font-size: 1.1rem;
    line-height: 1.2rem;
  }
}

h5 {
  font-family: inherit;
  font-weight: 400;
  font-style: italic;
  font-size: 1.3125rem;
  line-height: 1.875rem;
  margin-top: 1.875rem;
  margin-bottom: 0rem;
}

a {
  color: $root-color;
  text-decoration: none;
  &:visited {
    color: $root-color;
  }
  &:hover {
    color: $accent-color;
    //        text-decoration: underline;
  }
}

.section-body,
.project-description,
.project-right {
  a {
    color: $accent-color;
    &:visited {
      color: $accent-color;
    }
    &:hover {
      color: white;
      text-decoration: none;
      background: $accent-color;
    }
  }
}

p,
ul,
ol,
pre,
table,
blockquote {
  margin-top: 0rem;
  margin-bottom: 1.875rem;
}

ul ul,
ol ol,
ul ol,
ol ul {
  margin-top: 0;
  margin-bottom: 0;
}

li ul {
  margin-left: 1em;
}

hr {
  border: 1px solid $grey-color;
  margin: -1px 0 1em;
}

b,
strong,
em,
small,
code {
  line-height: 1;
}

sup {
  line-height: 0;
  position: relative;
  vertical-align: baseline;
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}
