.blog-wrapper {
  @include outer-container(1024px);
  padding: 5em 0.5em 0;
}

.blog-header {
}

.blog-tagline {
  margin: 1rem auto 1.875rem;
  text-align: center;
}

.top-dec {
  width: 100%;
  height: 85px;
  background: $theme-color;
  position: absolute;
}

.post-list {
  list-style: none;
  li {
    border-bottom: 1px solid $grey-color-light;
  }
}

.exerpt {
  padding-left: 1em;
}

.post-header {
}

.post-title {
  font-size: 3.4375rem;
  line-height: 3.75rem;
  margin-bottom: 1.875rem;
  letter-spacing: 0.1em;
  @include media($width-500) {
    font-size: 2.9rem;
    line-height: 2.7rem;
    letter-spacing: 0.08em;
  }
}

.post-meta {
  @include span-columns(3 of 3);
  font-size: 0.8em;
  color: $grey-color;
  display: flex;
  justify-content: space-between;
  @include media($tablet) {
    flex-direction: column;
    margin-bottom: 2rem;
  }
}

.post-date,
.post-read-time,
.post-category-author {
  @include span-columns(1 of 3);
  color: lighten($root-color, 15%);
  @include media($tablet) {
    text-align: center !important;
    width: 100%;
  }
}

.post-date {
  text-align: left;
}

.post-read-time {
  text-align: center;
}

.post-category-author {
  text-align: right;
}

.post-header-image {
  margin-bottom: 1em;
  padding: 0 1em;
  @include media($tablet) {
    padding: 0 0.25em;
  }
}

.category-link {
  color: lighten($root-color, 10%);
  &:visited {
    color: lighten($root-color, 10%);
  }
  &:hover {
    background: $accent-color;
    color: white;
  }
}

.post-content {
  @include span-columns(6 of 8);
  @include shift(1 of 8);
  @include media($tablet) {
    @include span-columns(8 of 8);
    @include shift(0 of 8);
  }
  img {
    display: block;
    margin: 0 auto;
  }
  p {
    a {
      color: $accent-color;
      &:hover {
        text-decoration: line-through;
      }
    }
  }
  li {
    margin-bottom: 0.75em;
  }
  .embed-container {
    margin-bottom: 1em;
  }
}

.readmore a {
  color: $grey-color-dark;
  &:hover {
    color: $accent-color;
  }
}
