// Button with offset background

.btn {
  margin: 0 1em 0.5em;
  font-weight: bold;
  font-size: 1em;
  letter-spacing: 0.1em;
  text-decoration: none;
  text-transform: uppercase;
  display: inline-block;
  padding: 0.75em 1.5em;
  position: relative;
  border: 4px solid $accent-color;
  color: $accent-color;
  border-radius: 12px;
  cursor: pointer;
  &::before {
    content: "";
    background: $theme-color;
    position: absolute;
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    z-index: -1;
    border-radius: 12px;
    left: 0.45em;
    top: 0.45em;
    @include transition(all 0.3s);
  }
  &:hover {
    &::before {
      left: -2px;
      top: -2px;
    }
  }
}

