#wrapper {
  overflow: hidden;
}

section {
  @include clearfix;
}

.page-content {
  position: relative;
  top: 0;
  width: 100%;
  overflow-x: hidden;
}

.chapter {
  position: relative;
  z-index: 200;
}

.section-title,
.section-body,
.project-left,
.project-right {
  @include outer-container(50%);
  margin: 0;
  @include media($full-width) {
    @include outer-container(100%);
  }
}

.section-title {
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  z-index: 200;
  @include media($full-width) {
    width: 100%;
    position: static;
  }
}

.section-title-fixed {
  position: fixed;
  @include media($full-width) {
    position: static;
  }
}

.title-container {
  @media screen and (max-width: 425px) {
    height: 50vh !important;
  }
  &::before {
    content: "";
    display: inline-block;
    height: 100%;
    vertical-align: middle;
    margin-left: -5px;
  }
}

.title-inner {
  display: inline-block;
  position: relative;
  width: 100%;
  vertical-align: middle;
  text-align: center;
}

.section-body {
  position: relative;
  width: 50%;
  left: 50%;
  z-index: 100;
  @include media($full-width) {
    width: 100%;
    left: 0;
  }
}

.project-left {
  //    position: absolute;
  //    top: 0;
  //    left: 0;
  width: 50%;
  z-index: 200;
  float: left;
  @include media($full-width) {
    width: 100%;
    position: static;
  }
}

.project-right {
  //    position: absolute;
  //    right: 0;
  //    top: 0;
  width: 50%;
  z-index: 100;
  float: left;
  overflow-y: hidden;
  @include media($full-width) {
    width: 100%;
    left: 0;
  }
}

.section-body {
  background-color: $theme-color;
}

.body-inner,
.project-inner {
  @include span-columns(6 of 8);
  @include shift(1 of 8);
  // margin of 1/8 all around
  margin-top: 12.5vh;
  margin-bottom: 12.5vh;
  @include media($full-width) {
    @include span-columns(8);
    @include shift(0);
    margin-top: 5vh;
    margin-bottom: 5vh;
    padding: 0 1em;
  }
}

.projects-inner {
  padding-top: 12.5vh;
  padding-bottom: 12.5vh;
}

.project-right-inner {
  padding-top: 12.5vh;
  padding-bottom: 12.5vh;
  overflow-y: hidden;
}
