.tagline {
  display: inline-block;
  max-width: rem(550);
  width: 65%;
  position: relative;
  svg {
    width: 100%;
  }
}

.amp-1 {
  fill: white;
}

.amp-2 {
  fill: $accent-color;
}

#splash {
  .nav-container {
    width: 100%;
    position: absolute;
    bottom: 5%;
    @include media(max-height 575px) {
      bottom: 2rem;
    }
    @media screen and (max-width: 425px) {
      display: none;
    }
  }
  nav {
    ul {
      text-align: center;
      margin: 0;
    }
    li {
      display: inline-block;
      list-style: none;
      letter-spacing: 0.05em;
      &::after {
        content: "/";
        display: inline-block;
      }
      &:last-of-type {
        &::after {
          content: "";
        }
      }
      a {
        padding: em(5) em(10);
        text-transform: uppercase;
        color: $root-color;
        transition: all 0.3s;
        &:hover,
        &:focus {
          background: transparent;
          color: $accent-color;
          text-decoration: line-through;
        }
        @include media($width-1400) {
          font-size: 1.5rem;
          line-height: 1rem;
        }
        @include media(max-height 575px) {
          font-size: 1.5rem;
          line-height: 1rem;
        }
      }
    }
  }
}

#about,
#contact {
  strong {
    color: $accent-color;
  }
}

.about-block {
  @include span-columns(4);
  @include media($mobile-large) {
    @include span-columns(8);
    @include omega();
    text-align: center;
  }
  h4 {
    margin-bottom: 0.5em;
  }
}

.about-block:nth-of-type(2) {
  @include omega();
}

.skill-list {
  margin-left: 0;
  margin-bottom: 1em;
  @include media($full-width) {
    margin-left: 1em;
  }
  @include media($mobile-large) {
    margin-left: 0;
    padding-left: 0;
    list-style: none;
  }
}

.work-project {
  display: block;
  height: 100%;
  @media screen and (max-width: 425px) {
    height: 50vh !important;
    min-height: unset !important;
  }
  a {
    display: block;
    height: 100%;
    position: relative;
    overflow: hidden;
  }
}

.work-cover-image {
  height: 100vh;
  overflow: hidden;
  background-position: center;
  background-size: cover;
  @include transform(scale(1));
  @include transition(transform 7s linear);
  &:hover {
    @include transform(scale(1.4));
  }
  img {
    min-height: 100%;
    height: 100vh;
    min-width: 100%;
    max-width: none;
    display: block;
  }
  &.view-all {
    + .work-project-title {
      background: transparent;
    }
  }
}

.work-project-title {
  width: 100%;
  position: absolute;
  top: 0;
  padding: 5em;
  text-align: center;
  color: white;
  background: rgba(#1a1a1a, 0.25);
  pointer-events: none;
  @include transform(scale(1));
  @include media($full-width) {
    padding: 0.5em;
  }
  @media screen and (max-width: 425px) {
    height: 50vh !important;
    min-height: unset !important;
  }
  .title-inner {
    // margin-top: 2.5em;
  }
  h2,
  h4 {
    text-shadow: 1px 1px 3px rgba(0, 16, 26, 0.7);
  }
  h2 {
    text-transform: uppercase;
    margin-bottom: 0;
  }
  h4 {
    font-weight: 400;
    margin-top: 0;
  }
}

#view-all-projects {
  a {
    @include transition(all 0.2s cubic-bezier(0.64, 0.225, 0.47, 0.735));
  }
}

.social-media-list {
  margin: 0;
  padding: 0;
  li {
    display: inline-block;
    margin-right: 1em;
    a {
      padding: 2px 5px 5px;
      display: flex;
      align-items: center;
    }
    &:hover {
      .icon {
        > svg {
          path {
            fill: white !important;
          }
        }
        &.icon--email,
        &.icon--linkedin {
          > svg {
            path,
            line {
              fill: transparent !important;
              stroke: white !important;
            }
          }
        }
      }
    }
  }
  .icon {
    display: block;
    > svg {
      width: 24px;
      height: 24px;
      margin-right: 10px;
    }
  }
}

