@charset "utf-8";

// Change Sanitize.scss Defaults

$root-background-color: #fdfdfd;
$root-font-family: "futura-pt", "Helvetica Neue", Helvetica, Arial, sans-serif;
$root-line-height: 30px;
$root-color: #00101a;

// Colors

$background-color: #fdfdfd;
$theme-color: #ffe900;
$accent-color: #540ead;

$grey-color: #828282;
$grey-color-light: lighten($grey-color, 40%);
$grey-color-dark: darken($grey-color, 25%);

// Import partials from `sass_dir` (defaults to `_sass`)
@import "base/sanitize", "base/sanitize_forms", "base/sanitize_page",
  "base/sanitize_typography", "vendors/bourbon/bourbon", "base/grid-settings",
  "vendors/neat/neat", "vendors/animate", "base/typography",
  "components/navigation", "components/buttons", "components/inputs",
  "components/icons", "layout/layout", "layout/header", "layout/footer",
  "pages/homepage", "pages/projects", "pages/blog";
