.projects-project {
  margin-top: 3em;
  text-align: center;
  &:first-of-type {
    margin-top: 0;
  }
  h2 {
    text-transform: uppercase;
    font-size: 2.6rem;
    line-height: 3rem;
    margin: 0;
  }
  p {
  }
}

.sidebar__inner {
  transform: translate(0, 0); /* For browsers don't support translate3d. */
  transform: translate3d(0, 0, 0);
  will-change: position, transform;
  // min-height: 100vh;
}

#project-container {
  display: flex;
  flex-wrap: wrap;
}

.project-left {
  position: relative;
  &.fixed {
    position: fixed;
  }
}

.project-left,
.project-right {
  width: 100vw;
  float: none;
  min-height: 100vh;
  @media screen and (min-width: 992px) {
    width: 50vw;
  }
}

.project-left {
  will-change: min-height;
  .title-container {
    padding: 20vh 0;
  }
}
// Individual project page
.project-description {
  @include span-columns(6 of 8);
  padding-bottom: 10vh;
  @include shift(1 of 8);
  position: relative;
  z-index: 0;
  @include media($full-width) {
    @include span-columns(8);
    @include shift(0);
    padding: 0 1em;
  }
  // top: -20vh;
  a {
    color: $accent-color;
    &:hover {
      background-color: $accent-color;
      color: white;
      text-decoration: none;
    }
  }
}

.project-navigation {
  text-align: center;
  .btn {
    margin: 1em auto;
    &:hover {
      background: none;
      color: $accent-color;
    }
  }
}

.project-image,
.project-video,
.project-gfycat {
  margin-bottom: 10vh;
  overflow: hidden;
  p {
    @include span-columns(6 of 8);
    @include shift(1 of 8);
    text-align: right;
    font-style: italic;
    font-size: 1em;
    margin-bottom: 0;
  }
}

.project-video {
  text-align: right;
  video {
    max-height: 90vh;
    width: auto;
  }
}
.project-image-container {
  display: inline-block;
}

.project-image-container,
.project-video-container,
.project-gfycat-container {
  &::after {
    content: "";
    display: block;
    height: 3vh;
    width: 97%;
    float: right;
    background-color: $theme-color;
  }
  img {
    display: block;
    width: 100%;
  }
}

.project-gfycat-container {
  iframe {
    position: absolute;
    top: 0;
    left: 0;
  }
}

.embed-container {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
  max-width: 100%;
  width: 100%;
}

.embed-container iframe,
.embed-container object,
.embed-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
